#image-container{
    height: 100%;
    display: flex;
    /* background-color: aquamarine; */
    justify-content: center;
    align-items: center;
    overflow: hidden
}

#image-container img{
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%
}
#product-box{
    box-shadow: rgba(0,0,0,0.15) 2px 2px 2px
}

#product-box:hover{
    box-shadow: rgba(0,0,0,0.2) 6px 6px 6px ;
    height: 90%;
    align-items: center;
    margin-bottom: 5px;
    margin-top: 5px;
    
    /* width: 102%; */
    transition: 0.5s;
    /* margin: -14px; */
    /* border: 4px solid; */
    border-color: rgba(2, 5, 185, 0.2) ;
    transition: border-color 0.5s height 0.3s width 0.3s;

}

#btn{
    border-radius: 2rem;
}
#btn:hover{
    background-color: blue;
    color: bisque;
    /* border: 12px solid; */
    border-color: rgba(2, 5, 185, 0.2) ;
    box-shadow: rgba(0,0,0,0.2) 2px 2px 2px ;
}

